/* src/App.css */

.App {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: linear-gradient(to bottom, #87ceeb, #f0f8ff); /* Gradient for sky effect */
  transition: background-color 0.5s ease;
}

.game-over {
  background-color: #ff6347; /* Tomato */
}

.game-over-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 4vw; /* Scale with viewport width */
  font-weight: bold;
  color: white;
}

.bird {
  position: absolute;
  width: 5vw; /* Scale with viewport width */
  height: 5vw; /* Ensure the bird remains square */
  user-select: none;
}

.pipe {
  position: absolute;
  width: 10vw; /* Scale with viewport width */
  height: 100vh; /* Pipes should extend the full height of the screen */
}

/* Clouds */
.cloud {
  position: absolute;
  background: white;
  border-radius: 50%;
  opacity: 0.8;
  animation: float 20s linear infinite;
}

.cloud1 {
  width: 15vw;
  height: 8vw;
  top: 10vh;
  left: 20vw;
  animation-duration: 25s;
}

.cloud2 {
  width: 20vw;
  height: 10vw;
  top: 20vh;
  left: 60vw;
  animation-duration: 30s;
}

.cloud3 {
  width: 12vw;
  height: 6vw;
  top: 30vh;
  left: 40vw;
  animation-duration: 20s;
}

@keyframes float {
  0% {
    transform: translateX(-100vw);
  }
  100% {
    transform: translateX(100vw);
  }
}

/* Mountains */
.mountain {
  position: absolute;
  bottom: 10vh; /* Place above the ground */
  width: 20vw;
  height: 20vh;
  background: #556b2f; /* Dark Olive Green for mountains */
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%); /* Triangle shape */
  z-index: 1;
}

.mountain1 {
  left: 10vw;
  height: 30vh; /* Varying height for perspective */
  width: 30vw;
  background-color: #6b8e23; /* Olive Drab for lighter mountain */
}

.mountain2 {
  left: 40vw;
  height: 25vh;
  width: 25vw;
}

.mountain3 {
  left: 70vw;
  height: 20vh;
  width: 20vw;
}

/* Trees */
.tree {
  position: absolute;
  bottom: 10vh; /* Place above the ground */
  width: 5vw;
  height: 15vh;
  background: #228b22; /* Forest Green for tree foliage */
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%); /* Triangle shape */
  z-index: 2;
}

.tree1 {
  left: 15vw;
  height: 12vh; /* Smaller tree */
  width: 3vw;
}

.tree2 {
  left: 55vw;
  height: 18vh; /* Taller tree */
  width: 4vw;
}

.tree3 {
  left: 85vw;
  height: 14vh;
  width: 3.5vw;
}

/* Ground */
.ground {
  position: absolute;
  bottom: 0;
  width: 100vw;
  height: 10vh;
  background: #deb887; /* Ground color */
  /* background-image: url('/assets/ground.png'); Optional ground texture */
  background-size: cover;
  z-index: 3; /* Ensure the ground is on top */
}
